.popular {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 85vh;
	margin-top : 10%;
	gap: 10px;
}

.popular h1 {
	color: #171717;
	font-size: 50px;
	font-weight: 600;

}

.popular hr {
	width: 200px;
	height: 6px;
	border-radius: 10px;
	background: #252525;
}

.popular-item {
	margin-top: 50px;
	display: flex;
	gap: 30px;
}


@media(max-width: 1280px){
	.popular {
		height: 80vh;
	}

	.popular h1 {
		font-size: 40px;
	}

	.popular hr {
		width: 160px;
		height: 4px;
	}

	.popular-item {
		gap: 20px;
		margin-top: 30px;
	}

}